export async function verifyOllamaSetup() {
  try {
    // First verify basic connection
    const response = await fetch('http://localhost:11434/api/version');
    if (!response.ok) {
      throw new Error('Ollama server connection failed');
    }

    // Test model with a simple prompt
    const testResponse = await fetch('http://localhost:11434/api/generate', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({
        model: 'deepseek-r1:14b',
        prompt: 'Hi, are you working?',
        stream: false
      })
    });

    if (!testResponse.ok) {
      throw new Error('Model test failed');
    }

    const result = await testResponse.json();
    console.log('Test response:', result);
    return true;
  } catch (error) {
    console.error('Setup verification failed:', error);
    return false;
  }
}
