import type { LLMConfig, LLMResponse, ChatMessage, ChatRequest, ChatResponse } from './types';

const MODEL = process.env.OLLAMA_MODEL || 'deepseek-r1:14b';

const SYSTEM_PROMPT = 'You are an AI assistant helping with audio development learning resources. ' +
  'You provide clear, technical, and accurate responses about audio programming, DSP, and music technology.';

async function ollamaRequest(endpoint: string, body: any): Promise<any> {
  const response = await fetch(`/api/${endpoint}`, {
    method: 'POST',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify(body)
  });

  if (!response.ok) {
    throw new Error(`API error: ${response.statusText}`);
  }

  return response.json();
}

export const ollamaClient = {
  async analyze(content: string, config: LLMConfig = {}): Promise<LLMResponse> {
    try {
      const response = await ollamaRequest('generate', {
        model: MODEL,
        prompt: `${SYSTEM_PROMPT}\n\nAnalyze this audio development resource:\n\n${content}`,
        options: {
          temperature: config.temperature ?? 0.7,
          max_tokens: config.maxTokens ?? 1000
        }
      });

      return {
        content: response.response,
        metadata: {
          tokens: response.eval_count || 0,
          model: MODEL
        }
      };
    } catch (error) {
      const err = error as Error;
      console.error('LLM processing failed:', err);
      throw new Error(`LLM processing failed: ${err.message}`);
    }
  },

  async chat(request: ChatRequest, resourceContext?: string): Promise<ChatResponse> {
    try {
      // Prepare messages with system prompt and resource context
      const messages = [
        {
          role: 'system' as const,
          content: resourceContext 
            ? `${SYSTEM_PROMPT}\n\nResource Context:\n${resourceContext}`
            : SYSTEM_PROMPT
        },
        ...request.messages
      ];

      const response = await ollamaRequest('chat', {
        model: MODEL,
        messages,
        options: {
          temperature: request.config?.temperature ?? 0.7,
          max_tokens: request.config?.maxTokens ?? 1000
        }
      });

      const assistantMessage = {
        role: 'assistant' as const,
        content: response.message.content
      };

      return {
        content: response.message.content,
        messages: [...request.messages, assistantMessage],
        metadata: {
          tokens: response.eval_count || 0,
          model: MODEL
        }
      };
    } catch (error) {
      const err = error as Error;
      console.error('Chat processing failed:', err);
      throw new Error(`Chat processing failed: ${err.message}`);
    }
  }
};
